import { type } from 'os'
import React from 'react'

import { ReactComponent as OpenContactSvg } from '../../resources/openContact.svg'
import BasicInstruction, { VariableOfInstruction } from './BasicInstruction'

type OpenContactProps = {
  variable?: VariableOfInstruction
  style?: object
}

const OpenContact = (props: OpenContactProps) => {
  const renderSvg = () => <OpenContactSvg />
  return <BasicInstruction {...props} name='openContact' svg={renderSvg}></BasicInstruction>
}

export default OpenContact
