import React, { useState } from 'react'
import { BorderOutlined } from '@ant-design/icons'
import { type } from 'os'

type WirePointProps = {
  style: object
}

const WirePoint = (props: WirePointProps) => {
  const [wirePointColor, setWirePointColor] = useState('#08c')
  const onDragOver = () => {
    setWirePointColor('yellow')
  }
  return (
    <BorderOutlined
      onMouseEnter={onDragOver}
      style={{
        ...props.style,
        fontSize: '16px',
        color: wirePointColor,
        position: 'absolute',
        backgroundColor: 'white',
        zIndex: 1,
      }}
    />
  )
}

export default WirePoint
