import React from 'react'

import OpenContact from '../instructions/OpenContact'
import Coil from '../instructions/Coil'
import { useCanvasByContext } from '../../store/hooks'

const mapInstruction = (instruction: string, options: Array<string>) => {
  switch (instruction) {
    case 'openContact':
      return (
        <OpenContact
          variable={{
            showVariable: true,
            options,
          }}
          style={{
            position: 'absolute',
            left: 0,
            top: 10,
          }}
        />
      )
    case 'coil':
      return (
        <Coil
          variable={{
            showVariable: true,
            options,
          }}
          style={{
            position: 'absolute',
            left: 0,
            top: 10,
          }}
        />
      )
    default:
      return <></>
  }
}

const InstructionContainer = (prop: any) => {
  const { instruction, componentIndex } = prop
  const visible = instruction !== undefined ? 'visible' : 'hidden'
  const canvasContext = useCanvasByContext()
  const currentSelectedComponent = canvasContext.getSelectedComponentIndex()
  const { variables } = canvasContext.getCanvas().networks[0]
  const isSelected = currentSelectedComponent === componentIndex

  const variableOptions = variables.map((e: any) => e.name)

  const onClick = () => {
    canvasContext.setSelectedComponentIndex(componentIndex)
  }
  return (
    <div
      onClick={onClick}
      style={{
        bottom: -150,
        left: 50,
        position: 'absolute',
        backgroundColor: isSelected ? '#faad14' : '#e6f7ff',
        zIndex: 1,
        height: 100,
        width: 100,
        visibility: visible,
      }}>
      {mapInstruction(instruction, variableOptions)}
    </div>
  )
}

export default InstructionContainer
