import { type } from 'os'
import React from 'react'

import { ReactComponent as CoilSvg } from '../../resources/coil.svg'
import BasicInstruction, { VariableOfInstruction } from './BasicInstruction'

type CoilProps = {
  variable?: VariableOfInstruction
  style?: object
}

const Coil = (props: CoilProps) => {
  const renderSvg = () => <CoilSvg />
  return <BasicInstruction {...props} name='coil' svg={renderSvg}></BasicInstruction>
}

export default Coil
