import React, { useCallback, useState } from 'react'

import LadUnit from './LadUnit'
import { useCanvasByContext } from '../../store/hooks'

const Network = () => {
  const lineMatrix1 = {
    horizontalLine: {
      top: [false, false],
      mid: [true, true],
      bottom: [false, false],
    },
    verticalLine: {
      left: [true, true],
      mid: [false, false],
      right: [false, false],
    },
  }
  const lineMatrix2 = {
    horizontalLine: {
      top: [false, false],
      mid: [true, true],
      bottom: [false, false],
    },
    verticalLine: {
      left: [false, false],
      mid: [false, false],
      right: [false, false],
    },
  }
  const initWirePointDisplayMatrix = [
    [false, false, false],
    [false, false, false],
  ]

  const [wirePointMatrix, setWirePointMatrix] = useState(initWirePointDisplayMatrix)
  const canvasContext = useCanvasByContext()
  const canvasComponents = canvasContext.getComponents()

  const onDragOver = (e: React.DragEvent) => {
    e.stopPropagation()
    e.preventDefault()
  }

  const onDragEnter = (e: React.DragEvent) => {
    setWirePointMatrix([
      [true, true, true],
      [false, true, true],
    ])

    e.preventDefault()
  }

  const onDragLeave = (e: React.DragEvent) => {
    setWirePointMatrix(initWirePointDisplayMatrix)

    e.preventDefault()
  }

  const onDrop = useCallback((e: React.DragEvent) => {
    setWirePointMatrix(initWirePointDisplayMatrix)

    const dragComponentString = e.dataTransfer.getData('dragComponent')
    const component = JSON.parse(dragComponentString)
    canvasContext.addComponent(component)
  }, [])

  const onKeyUp = (e: React.KeyboardEvent) => {
    const { code } = e
    switch (code) {
      case '8':
        canvasContext.deleteComponent()
        break
      default:
    }
  }

  const renderNetwork = () => {
    if (canvasComponents === undefined || canvasComponents.length === 0) {
      return (
        <>
          <LadUnit wirePointMatrix={wirePointMatrix[0]} lineMatrix={lineMatrix1} />
        </>
      )
    }
    return (
      <>
        {canvasComponents.map((cmp: { instruction: string }, index: number) => {
          return (
            <LadUnit
              key={index}
              index={index}
              wirePointMatrix={wirePointMatrix[0]}
              lineMatrix={index === 0 ? lineMatrix1 : lineMatrix2}
              instruction={cmp.instruction}
            />
          )
        })}
      </>
    )
  }

  const calculateWidth = () => {
    return 200 * canvasComponents.length
  }

  return (
    <div
      tabIndex={0}
      onKeyUp={onKeyUp}
      id='network'
      onDragEnter={onDragEnter}
      onDragOver={onDragOver}
      onDragLeave={onDragLeave}
      onDrop={onDrop}
      style={{ width: calculateWidth(), height: 200 }}>
      <div>
        <b>Network</b>
      </div>
      {renderNetwork()}
    </div>
  )
}

export default Network
