import React, { useEffect, useReducer } from 'react'
import './App.css'
import { Layout } from 'antd'

import Network from './component/ladElement/Network'
import { useCanvas } from './store/hooks'
import { CanvasContext } from './store/Context'
import InstructionToolBox from './component/InstructionToolBox'
import VariableTable from './component/VariableTable'

const { Content, Sider } = Layout

function App() {
  const canvas: any = useCanvas()
  const [, forceUpdate] = useReducer((x) => x + 1, 0)

  useEffect(() => {
    const unsubscribe = canvas.subscribe(() => {
      forceUpdate()
    })

    return () => {
      unsubscribe()
    }
  }, [])

  return (
    <Layout hasSider>
      <CanvasContext.Provider value={canvas}>
        <Layout className='site-layout' style={{ marginRight: 200, overflow: 'hide' }}>
          {/* <Header className="site-layout-background" style={{ padding: 0 }} /> */}
          <Content>
            <b>Variable Table</b>
            <VariableTable></VariableTable>
          </Content>
          <Content style={{ margin: '0 0 0', overflow: 'hidden' }}>
            <div className='site-layout-background' style={{ padding: 24, textAlign: 'center', overflowX: 'auto' }}>
              <Network />
            </div>
          </Content>
        </Layout>
        <Sider
          style={{
            overflow: 'auto',
            height: '100vh',
            position: 'fixed',
            right: 0,
            top: 0,
            bottom: 0,
            backgroundColor: 'white',
          }}>
          <InstructionToolBox />
        </Sider>
      </CanvasContext.Provider>
    </Layout>
  )
}

export default App
