import { cloneDeep } from 'lodash'

const getOnlyKey = () => {
  return Math.random()
}

type Variable = {
  name: string
  key: string
  dataType: 'bool' | 'string' | 'int'
  defaultValue?: string
  comment?: string
}

type Network = {
  key: string
  components: any[]
  variables: Array<Variable>
}

type CanvasDisplayData = {
  title: string
  networks: Array<Network>
}

const getDefaultCanvasDisplay = (): CanvasDisplayData => {
  return {
    title: 'unnamed',
    networks: [
      {
        key: '0',
        components: [],
        variables: [
          {
            key: '0',
            name: 'aaa',
            dataType: 'bool',
            defaultValue: 'true',
            comment: 'the first variable',
          },
          {
            key: '1',
            name: 'bbb',
            dataType: 'bool',
            defaultValue: 'false',
            comment: 'the second variable',
          },
          {
            key: '2',
            name: 'ccc',
            dataType: 'bool',
            defaultValue: 'false',
            comment: 'the third variable',
          },
        ],
      },
    ],
  }
}

export default class Canvas {
  canvasDisplayData: CanvasDisplayData

  listeners: any[]

  canvasChangeHistory: [CanvasDisplayData]

  canvasChangeHistoryIndex: number

  maxCanvasChangeHistoryIndex: number

  selectedComponentIndexes: Set<number>

  constructor(_canvasDisplayData: CanvasDisplayData = getDefaultCanvasDisplay()) {
    this.canvasDisplayData = _canvasDisplayData
    this.listeners = []
    this.canvasChangeHistory = [cloneDeep(this.canvasDisplayData)]
    this.canvasChangeHistoryIndex = 0
    this.maxCanvasChangeHistoryIndex = 100
    this.selectedComponentIndexes = new Set()
  }

  getCanvas = () => {
    return { ...this.canvasDisplayData }
  }

  getComponents = () => {
    return [...this.canvasDisplayData.networks[0].components]
  }

  getSelectedComponentIndex = () => {
    const selectedCmpIndex = Array.from(this.selectedComponentIndexes)[0]

    return selectedCmpIndex === undefined ? -1 : selectedCmpIndex
  }

  getSelectedComponent = () => {
    const cmps = this.getComponents()

    return cmps[this.getSelectedComponentIndex()]
  }

  addToSelectedComponentSet = (indexes: [number] | number) => {
    if (Array.isArray(indexes)) {
      indexes.forEach((index) => index !== -1 && this.selectedComponentIndexes.add(index))
    } else if (indexes !== -1) {
      this.selectedComponentIndexes.add(indexes)
    }
  }

  updateApp = () => {
    this.listeners.forEach((listener) => listener())
  }

  setSelectedComponentIndex = (index: number) => {
    if (this.getSelectedComponentIndex() === index) {
      return
    }

    this.selectedComponentIndexes.clear()

    if (index > -1) {
      this.addToSelectedComponentSet(index)
    }

    this.updateApp()
  }

  setCanvas = (_canvasDisplayData: CanvasDisplayData) => {
    Object.assign(this.canvasDisplayData, _canvasDisplayData)

    this.updateApp()
    this.recordCanvasChangeHistory()
  }

  recordCanvasChangeHistory = () => {
    this.canvasChangeHistory[++this.canvasChangeHistoryIndex] = cloneDeep(this.canvasDisplayData)

    if (this.canvasChangeHistory.length > this.maxCanvasChangeHistoryIndex) {
      this.canvasChangeHistory.shift()
      this.canvasChangeHistoryIndex--
    }
  }

  addComponent = (_component: any) => {
    const component = { ..._component, key: getOnlyKey() }

    this.canvasDisplayData.networks[0].components.push(component)

    this.selectedComponentIndexes.clear()
    this.addToSelectedComponentSet(this.canvasDisplayData.networks[0].components.length - 1)

    this.updateApp()
    this.recordCanvasChangeHistory()
  }

  deleteComponent = () => {
    const sorted = Array.from(this.selectedComponentIndexes).sort((a, b) => b - a)
    sorted.forEach((index) => {
      this.canvasDisplayData.networks[0].components.splice(index, 1)
    })

    this.selectedComponentIndexes.clear()

    this.updateApp()
    this.recordCanvasChangeHistory()
  }

  subscribe = (listener: any) => {
    this.listeners.push(listener)

    return () => {
      this.listeners = this.listeners.filter((lis) => lis !== listener)
    }
  }

  getPublicCanvasOperation = () => {
    const operations = {
      getCanvas: this.getCanvas,
      setCanvas: this.setCanvas,
      getComponents: this.getComponents,
      addComponent: this.addComponent,
      deleteComponent: this.deleteComponent,
      getSelectedComponent: this.getSelectedComponent,
      getSelectedComponentIndex: this.getSelectedComponentIndex,
      setSelectedComponentIndex: this.setSelectedComponentIndex,
      subscribe: this.subscribe,

      recordCanvasChangeHistory: this.recordCanvasChangeHistory,
      // goPrevCanvasHistory: this.goPrevCanvasHistory,
      // goNextCanvasHistory: this.goNextCanvasHistory,
    }
    return operations
  }
}
