import React from 'react'
import _ from 'lodash'
import LadCell from './LadCell'
import WirePoint from './WirePoint'

import InstructionContainer from './InstructionContainer'

type LineMatrix = {
  horizontalLine: {
    top: boolean[]
    mid: boolean[]
    bottom: boolean[]
  }
  verticalLine: {
    left: boolean[]
    mid: boolean[]
    right: boolean[]
  }
}

type LadUnitProps = {
  instruction?: string
  index?: number
  lineMatrix?: LineMatrix
  wirePointMatrix?: boolean[]
}

const handleCellLineStyle = (lineMatrix: LineMatrix) => {
  let cellOneStyle = {}
  let cellTwoStyle = {}
  let cellThreeStyle = {}
  let cellFourStyle = {}

  if (lineMatrix.horizontalLine.top[0]) {
    cellOneStyle = { ...cellOneStyle, borderTopStyle: 'solid' }
  }
  if (lineMatrix.horizontalLine.top[1]) {
    cellTwoStyle = { ...cellOneStyle, borderTopStyle: 'solid' }
  }

  if (lineMatrix.horizontalLine.mid[0]) {
    cellOneStyle = { ...cellOneStyle, borderBottomStyle: 'solid' }
  }
  if (lineMatrix.horizontalLine.mid[1]) {
    cellTwoStyle = { ...cellOneStyle, borderBottomStyle: 'solid' }
  }

  if (lineMatrix.horizontalLine.bottom[0]) {
    cellThreeStyle = { ...cellThreeStyle, borderBottomStyle: 'solid' }
  }
  if (lineMatrix.horizontalLine.bottom[1]) {
    cellFourStyle = { ...cellFourStyle, borderBottomStyle: 'solid' }
  }

  if (lineMatrix.verticalLine.left[0]) {
    cellOneStyle = { ...cellOneStyle, borderLeftStyle: 'solid' }
  }
  if (lineMatrix.verticalLine.left[1]) {
    cellThreeStyle = { ...cellThreeStyle, borderLeftStyle: 'solid' }
  }

  if (lineMatrix.verticalLine.mid[0]) {
    cellOneStyle = { ...cellOneStyle, borderRightStyle: 'solid' }
  }
  if (lineMatrix.verticalLine.mid[1]) {
    cellThreeStyle = { ...cellThreeStyle, borderRightStyle: 'solid' }
  }

  if (lineMatrix.verticalLine.right[0]) {
    cellTwoStyle = { ...cellTwoStyle, borderRightStyle: 'solid' }
  }
  if (lineMatrix.verticalLine.right[1]) {
    cellFourStyle = { ...cellFourStyle, borderRightStyle: 'solid' }
  }

  return [cellOneStyle, cellTwoStyle, cellThreeStyle, cellFourStyle]
}

const handleWirePointStyle = (wirePointMatrix: boolean[]) => {
  const wirePointStyles = _.map(wirePointMatrix, (e) => {
    if (e) {
      return { visibility: 'visible' }
    }
    return { visibility: 'hidden' }
  })
  return wirePointStyles
}

const LadUnit = (props: LadUnitProps) => {
  const { instruction, index } = props
  const initLineMatrix = {
    horizontalLine: {
      top: [false, false],
      mid: [false, false],
      bottom: [false, false],
    },
    verticalLine: {
      left: [false, false],
      mid: [false, false],
      right: [false, false],
    },
  }

  const lineMatrix = props.lineMatrix ?? initLineMatrix
  const cellStyles = handleCellLineStyle(lineMatrix)

  const wirePointMatrix = props.wirePointMatrix ?? [false, false, false]
  const wirePointStyle = handleWirePointStyle(wirePointMatrix)

  return (
    <div style={{ width: 200, height: 200, float: 'left' }}>
      <div style={{ position: 'relative' }}>
        <InstructionContainer componentIndex={index} instruction={instruction} />
        <WirePoint
          style={{
            ...wirePointStyle[0],
            bottom: -105,
            left: -7,
          }}
        />
        <WirePoint
          style={{
            ...wirePointStyle[1],
            bottom: -105,
            left: 93,
          }}
        />
        <WirePoint
          style={{
            ...wirePointStyle[2],
            bottom: -105,
            left: 193,
          }}
        />
      </div>
      <LadCell borderStyle={cellStyles[0]} />
      <LadCell borderStyle={cellStyles[1]} />
      <LadCell borderStyle={cellStyles[2]} />
      <LadCell borderStyle={cellStyles[3]} />
    </div>
  )
}

export default LadUnit
