import React from 'react'
import { Select } from 'antd'

const { Option } = Select

export type VariableOfInstruction = {
  showVariable: boolean
  options: Array<string>
}

interface InstructionProps {
  name: string
  svg: () => JSX.Element
  variable?: VariableOfInstruction
  style?: object
}

const BasicInstruction = (props: InstructionProps) => {
  const onDragStart = (e: React.DragEvent<HTMLDivElement>, _cmp: object) => {
    e.dataTransfer?.setData('dragComponent', JSON.stringify(_cmp))
  }

  const { variable, name, svg } = props

  const showVariable = variable === undefined ? false : variable.showVariable

  return (
    <div
      style={{
        ...props.style,
        width: 100,
      }}
      draggable='true'
      onDragStart={(e: React.DragEvent<HTMLDivElement>) => {
        onDragStart(e, { instruction: name })
      }}>
      {showVariable && (
        <Select
          key='select'
          showSearch
          placeholder='???'
          optionFilterProp='children'
          size='small'
          showArrow={false}
          filterOption={(input, option) =>
            (option?.children as unknown as string).toLowerCase().includes(input.toLowerCase())
          }>
          {variable?.options.map((option, index) => (
            <Option key={index} value={option}>
              {option}
            </Option>
          ))}
        </Select>
      )}
      {svg()}
    </div>
  )
}

export default BasicInstruction
