import React, { useContext, useRef } from 'react'
import { CanvasContext } from './Context'
import Canvas from './canvas'

export function useCanvas() {
  const canvasRef: React.MutableRefObject<object | undefined> = useRef()
  if (!canvasRef.current) {
    const canvas = new Canvas()
    canvasRef.current = canvas.getPublicCanvasOperation()
  }

  return canvasRef.current
}

export function useCanvasByContext() {
  const canvas = useContext(CanvasContext)

  return canvas
}

export function useCanvasData() {
  const canvas = useContext(CanvasContext)
  return canvas.getCanvas()
}

export function useCanvasComponents() {
  const canvas = useContext(CanvasContext)
  return canvas.getComponents()
}
